import React, { useEffect, useRef } from "react";
import gsap from "gsap";

export default function Ticker({ duration = 10, children, direction = -100 }) {
  const wrapperRef = useRef(null);

  useEffect(() => {
    const wrapper = wrapperRef.current;
    if (!wrapper) return;

    const first = wrapper.querySelector(".ticker-text");

    if (wrapper.children.length < 2) {
      const clone = first.cloneNode(true);
      wrapper.appendChild(clone);
    }

    const elements = wrapper.querySelectorAll(".ticker-text");
    const animations = [];

    elements.forEach((el, index) => {
      const startX = direction < 0 ? 0 : -100;
      const endX = direction < 0 ? -100 : 0;

      const anim = gsap.fromTo(
        el,
        { x: `${startX}%` },
        {
          x: `${endX}%`,
          repeat: -1,
          duration,
          ease: "linear",
          delay: index > 0 ? 0 : 0,
        }
      );
      animations.push(anim);
    });

    return () => {
      animations.forEach((a) => a.kill());
    };
  }, [duration, direction]);

  return (
    <div className="ticker">
      <div className="ticker-wrap" ref={wrapperRef}>
        <div className="ticker-text">{children}</div>
      </div>
    </div>
  );
}
